<template>
    <p v-if="cartOrders == ''" class="msg"></p>
    <ListUpdate v-else class="cart__wrapper">
        <template v-for="(cartOrder, cartOrder_index) in cartOrders" :key="cartOrder_index">
            <CartShow
                class="cart__item"
                :class1="cartOrder.class1"
                :class2="cartOrder.class2"
                :class3="cartOrder.class3"
                :setmenu_values="cartOrder.setmenu_values"
                :quantity="cartOrder.quantity"
                :price="cartOrder.price"
                @quantityChange="quantityChange"
            />
        </template>
    </ListUpdate>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
import CartShow from "../molecules/CartShow"
import ListUpdate from "../transition/ListUpdate"

export default {
    components: {
        CartShow,
        ListUpdate,
    },
    data: () => {
    return {
        }
    },
    watch: {
        lightBox:{
            handler(){
                if(this.lightBox == 'CartMenus'){
                    setTimeout(() => 
                        this.makePageCardGridLayout(),
                    200)
                }
            },
        },
        cartOrders:{
            handler(){
                setTimeout(() => 
                    this.makePageCardGridLayout(),
                1)
            },
            deep: true,
            immediate: true,
        },
    },
    mounted: function () {
        window.addEventListener("resize", ()=>{
            let timeoutID = 0;
            let delay = 100;
            clearTimeout(timeoutID);
            timeoutID = setTimeout(()=>{
                this.makePageCardGridLayout()
                console.log("resize");
    
            }, delay);
        }, false);
    },
    computed: {
        ...mapGetters([ 'cartOrders' ,'selectClass', 'lightBox']),
    },
    methods: {
        ...mapActions([ 'addCart', 'cartBallPositionUpdate' ]),
        quantityChange(event){
            console.log(event)
            this.addCart(event)
        },
        makePageCardGridLayout() {
            console.log('makePageCardGridLayout')
            let evenPosX = 0;
            let evenPosY = 0;
            let oddPosY = 0;
            const PosX = 0;
            let PosY = 0;
            let sh
            const cart__items = document.getElementsByClassName('cart__item');
            if(cart__items.length > 0) {
                let gridWindow;
                for (let i = 0; i < cart__items.length; i++) {
                    i == 0 ? gridWindow = cart__items[0].closest('.cart__wrapper') : i; // Grid
                    if (window.matchMedia('(max-width: 959px)').matches) {
                        console.log('under 959px')
                        cart__items[i].setAttribute("style", "transform: translateX(" + PosX + "px) translateY(" + PosY + "px)");  // 
                        PosY = PosY + cart__items[i].clientHeight + 10;  // Y
                        console.log(PosY)
                        sh = PosY + 150
                        gridWindow.style.height = sh+'px'; // 
                    } else {
                        const oddPosX = cart__items[0].clientWidth * 495 / 485;
                        if (i % 2 == 0) { // Grid
                            cart__items[i].setAttribute("style", "transform: translateX("+ evenPosX +"px) translateY(" + evenPosY + "px)");  // 
                            evenPosY = evenPosY + cart__items[i].clientHeight + 10;  // Y
                        } else if (i % 2 != 0) {  // Grid
                            cart__items[i].setAttribute("style", "transform: translateX(" + oddPosX + "px) translateY(" + oddPosY + "px)");  // 
                            oddPosY = oddPosY + cart__items[i].clientHeight + 10;  // Y
                        }
                        evenPosY > oddPosY ? sh = evenPosY + 150 : sh = oddPosY + 150; // 
                        gridWindow.style.height = sh+'px'; // 
                    }
                }
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.cart{
    &__wrapper{
        width: 100%;
        height: calc(100% - 65px);
        @include mq('tb'){
        }
        &::after{
            @include after;
        }
    }
    &__item{
        width: calc(48.5% - 10px);
        padding: 0 5px;
        position: absolute;
        margin-bottom: 10px;
        @include mq('tb'){
            width: calc(98% - 10px);
        }
    }
}
.msg{
    font-size: 1.6rem;
    color: $text-primary;
    font-weight: bold;
}
.ListUpdate-leave-active {
  transition: all $animation-time ease;
  position:absolute;
}
.ListUpdate-move {
  transition: transform $animation-time ease;
}
</style>